@media screen and (min-width: 961px) and (max-width: 1280px) {
  .our-works-nav {
    
    li {
      margin-right: 20px;
      a {
        min-width: unset;
        width: auto;
        padding: 7px 12px;
      }
    }
  }
}
@media screen and (min-width: 560px) and (max-width: 960px) {
  .our-works-nav {
    margin: -7px;
    margin-bottom: 44px;
    justify-content: flex-start;
    li {
      margin: 7px;
      a {
        height: auto;
        font-size: 0.8rem;
        padding: 5px 10px;
        width: auto;
        min-width: unset;
      }
    }
  }
}
@media screen and (max-width: 560px) {
  #intro {
    min-height: 100vh;
  }
  h1 {
    font-size: 2rem;
  }

  .service-item {
    width: auto;
    height: auto;
    padding: 40px 15px;
    margin-top: 0px;
  }
  .our-works-nav li {
    margin: 0px;
  }

  footer .main-footer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-content: flex-start;
    text-align: left;
    .logo, .footer-menu, .phone {
      margin-bottom : 15px;
    }
    .footer-menu {
      li {
        width: 100%;
      }
    }

    .footer-social {
      justify-content: flex-start;
    }
  }
}

#contacts {
  .contacts-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    .contacts-block {
      flex: 0 80%;
      .email {
        font-size: 6rem;
      }
      @media screen and (max-width: 560px) {
        flex: 0 100%;
        width: 100%;
        padding: 15px;
        h3 {
          font-size: 1.3rem;
        }
        form {
          width: 100%;
        }
        input, textarea {
          max-width: unset;
        }
        .email, .phone {
          transform: none;
          margin-bottom: 10px;
          position: relative;
          font-size: 1rem;
          color: #fff;
        }
      }

    }
  }
}
