#reviews {
  //display: none;
  margin-top: 114px;
  margin-bottom: 119px;
  h2 {
    @media screen and (min-width: 560px) {
      background: #000;
      color: #fff;
      font-size: 100px;
      line-height: 70px;
      width: fit-content;
      padding-left: 55px;
      margin-right: 10px;
      margin-bottom: 55px;
      position: relative;
      &:after {
        height: 100%;
        width: 8px;
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        background: #fff;
      }
    }
  }

  a {
    color: black;
    font-size: 14px;
    text-decoration: underline;
  }

  img {
    width: 100%;
    max-width: 100%;
  }
}
