#our-equipment {
  margin: 0 auto;
  //max-width: 80%;
  padding-top: 80px;
  padding-bottom: 80px;
  h2 {
    text-align: center;
    font-weight: 900;
    font-size: 36px;
    color: #141414;
    margin-bottom: 114px;
  }
  .item {
    text-align: center;
    img {
      margin: 10px auto;
    }
  }
  
  
  @media screen and (min-width: 560px) {
    .slick-list {
      overflow: visible;
      .item {
        overflow: visible;
      }
    }
    .slick-slide {
      transition: 0.5s all ease;
      transform: scale(0);
    }
    .slick-slide {
      overflow: visible;
      img {
        transition: 0.5s all ease-in;
        opacity: 0.4;
      }
      .equipment-content {
        visibility: hidden;
        font-size: 14px;
        max-width: 350px;
        margin: 0 auto;
        h4 {
          margin-bottom: 20px;
          font-size: 1.2rem;
        }
      }
    }
    .slick-slide.slick-active {
      transform: scale(0.5);
      .equipment-content {
        visibility: hidden;
      }

      &+.slick-active {
        transform: none;
        img {
          opacity: 1;
          transform: scale(1.2);
          overflow: visible;
        }
        .equipment-content {
          visibility: visible;
        }
      }
      &+.slick-active+.slick-active {
        transform: scale(0.5);
        img {
          opacity: 0.4;
          transform: scale(1);
        }
        .equipment-content {
          visibility: hidden !important;
        }
      }
    }
  }
  
.slick-arrow {
  margin-top: 10%;
    &:before {
      font-family: 'FontAwesome';
      color: black;
    }
    &.slick-next {
      right: 30%;
      &:before {
        content: '\f054';
      }
    }
    &.slick-prev {
      left: 30%;
      &:before {
        content: '\f053';
      }
    }
  }
}
