/********************
Fonts
********************/
@font-face {
  font-family: 'Aeroport';
  src: url('../fonts/Aeroport.eot');
  src: local('Aeroport'),
      url('../fonts/Aeroport.eot?#iefix') format('embedded-opentype'),
      url('../fonts/Aeroport.woff2') format('woff2'),
      url('../fonts/Aeroport.woff') format('woff'),
      url('../fonts/Aeroport.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Aeroport';
  src: url('../fonts/Aeroport-Medium.eot');
  src: local('Aeroport Medium'), local('Aeroport-Medium'),
      url('../fonts/Aeroport-Medium.eot?#iefix') format('embedded-opentype'),
      url('../fonts/Aeroport-Medium.woff2') format('woff2'),
      url('../fonts/Aeroport-Medium.woff') format('woff'),
      url('../fonts/Aeroport-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'Aeroport';
  src: url('../fonts/Aeroport-Bold.eot');
  src: local('Aeroport Bold'), local('Aeroport-Bold'),
      url('../fonts/Aeroport-Bold.eot?#iefix') format('embedded-opentype'),
      url('../fonts/Aeroport-Bold.woff2') format('woff2'),
      url('../fonts/Aeroport-Bold.woff') format('woff'),
      url('../fonts/Aeroport-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

/********************
General
********************/
html {
  height: 100%;
}
body {
  min-height: 100%;
  min-width: 320px;
  font-family: 'Aeroport', sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.5;
  color: #141414;
  background-color: #ffffff;
  overflow-x: hidden;
}
h1, h2, h3, h4, h5, h6 {
  font-family: 'Aeroport', sans-serif;
  font-weight: bold;
}
ul,
ol {
	margin: 0;
	padding: 0;
	list-style-type: none;
}
a, button {
	transition: all 0.3s;
}
a:hover {
	text-decoration: none;
}
p {
	margin-bottom: 0;
}
img {
	max-width: 100%;
}
/* Hide element */
.visually-hidden {
	position: absolute;
	clip: rect(0 0 0 0);
	width: 1px;
	height: 1px;
	margin: -1px;
}
/* Hide text in element */
.hide-text {
	text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
}
/* Markup */
.container {
	max-width: 1250px;
}

/*** Form elements ***/
.btn {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-ms-align-items: center;
	align-items: center;
	justify-content: center;
	width: 225px;
	height: 56px;
	padding: 0 30px;
	font-size: 16px;
	font-weight: 500;
	line-height: 1;
	text-transform: lowercase;
	color: #bfb8a6;
	border: 2px solid #bfb8a6;
}
.btn:hover {
	color: #fff;
	background-color: #bfb8a6;
}
.btn:focus {
	outline: none;
}
.form-group {
	margin-bottom: 32px;
}
input[type="text"],
textarea {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: center;
	-ms-align-items: center;
	align-items: center;
	width: 100%;
	max-width: 444px;
	height: 71px;
	padding: 0 15px;
	font-size: 18px;
	font-weight: bold;
	text-align: center;
	color: #141414;
	border: none;
	outline: none;
}
textarea {
	height: 157px;
	padding: 15px;
}
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #141414;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #141414;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #141414;
}
:-moz-placeholder { /* Firefox 18- */
  color: #141414;
}

/*** Header ***/
header {
	margin-bottom: 130px;
	padding-top: 45px;
}
header > .container {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	-moz-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	-o-flex-wrap: wrap;
	flex-wrap: wrap;
	-ms-align-items: center;
	align-items: center;
}
header .logo {
	margin-right: auto;
}
.header-social,
.footer-social {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	margin-right: 105px;
}
.header-social .icon-soc,
.footer-social .icon-soc {
	width: 18px;
	height: 18px;
	background: url('../images/icons/icon-vk.png') no-repeat center;
	-webkit-background-size: cover;
	background-size: cover;
}
.header-social .icon-soc.icon-vk,
.footer-social .icon-soc.icon-vk {
	margin-right: 10px;
}
.header-social .icon-soc.icon-instagram,
.footer-social .icon-soc.icon-instagram {
	background: url('../images/icons/icon-instagram.png') no-repeat center;
}
.header-social .icon-soc:hover,
.footer-social .icon-soc:hover {
	opacity: 0.7;
}
.header-phone {
	font-size: 18px;
	font-weight: bold;
	color: #fff;
}
header nav {
	-ms-flex: 0 0 100%;
  flex: 0 0 100%;
  max-width: 100%;
	margin-top: 83px;
}
.main-menu li {
	position: relative;
	margin-bottom: 8px;
}
.main-menu li a {
	font-size: 18px;
	font-weight: bold;
	color: #fff;
}
.main-menu li.active:before {
	content: '';
	position: absolute;
	left: -30px;
	top: 50%;
	transform: translateY(-50%);
	display: block;
	width: 20px;
	height: 2px;
	background-color: #bfb8a6;
}
.main-menu li.active a,
.main-menu li a:hover {
	padding-left: 8px;
	color: #bfb8a6;
}

/********************
Main page
********************/

/*** Intro ***/
#intro {
	position: relative;
	min-height: 1085px;
	background: url('../images/quadrocopter.png'), url('../images/intro-bg.jpg');
	background-repeat: no-repeat;
	background-position: right 1085px, center;
	-webkit-background-size: 500px 330px, cover;
	background-size: 500px 330px, cover;
	animation:
						quadrocopter-start 5s ease forwards,
						quadrocopter-move1 20s ease 5s forwards,
						quadrocopter-move2 10s ease 25s infinite;
}
h1 {
	margin-bottom: 15px;
	font-size: 72px;
	line-height: 0.9;
	color: #fff;
}
.subtitle {
	margin-bottom: 35px;
	font-weight: bold;
	color: #fff;
}
.btn-callback {
	margin-bottom: 26px;
}
.btn-desc {
	font-size: 14px;
	font-weight: 500;
	color: #fff;
}
@keyframes quadrocopter-start {
	0% {
		background-position: right 1085px, center;
		-webkit-background-size: 500px 330px, cover;
		background-size: 500px 330px, cover;
	}
	100% {
		background-position: right 215px, center;
		-webkit-background-size: 1240px 819px, cover;
		background-size: 1240px 819px, cover;
	}
}
@keyframes quadrocopter-move1 {
	0% {
		background-position: right 215px, center;
		-webkit-background-size: 1240px 819px, cover;
		background-size: 1240px 819px, cover;
	}
	25% {
		background-position: -100% 1085px, center;
		-webkit-background-size: 300px 198px, cover;
		background-size: 300px 198px, cover;
	}
	50% {
		background-position: 180% -1000px, center;
		-webkit-background-size: 700px 462px, cover;
		background-size: 700px 462px, cover;
	}
	75% {
		background-position: 40% 1285px, center;
		-webkit-background-size: 1200px 792px, cover;
		background-size: 1200px 792px, cover;
	}
	100% {
		background-position: right 215px, center;
		-webkit-background-size: 1240px 819px, cover;
		background-size: 1240px 819px, cover;
	}
}
@keyframes quadrocopter-move2 {
	0% {
		background-position: right 215px, center;
	}
	10% {
		background-position: 95% 235px, center;
	}
	20% {
		background-position: 90% 255px, center;
	}
	25% {
		background-position: 85% 265px, center;
	}
	30% {
		background-position: 90% 275px, center;
	}
	40% {
		background-position: 95% 295px, center;
	}
	50% {
		background-position: 100% 315px, center;
	}
	60% {
		background-position: 105% 295px, center;
	}
	70% {
		background-position: 110% 275px, center;
	}
	75% {
		background-position: 115% 265px, center;
	}
	80% {
		background-position: 110% 255px, center;
	}
	90% {
		background-position: 105% 235px, center;
	}
	100% {
		background-position: 100% 215px, center;
	}
}

/*** Services ***/
#services {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: center;
	-webkit-flex-wrap: wrap;
	-moz-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	-o-flex-wrap: wrap;
	flex-wrap: wrap;
	margin-bottom: 100px;
}
.service-item {
	position: relative;
	width: 420px;
	height: 420px;
	margin-top: -108px;
	padding: 62px;
	color: #fff;
}
.service-item.tours {
	background: #141414 url('../images/service-tours-signature.png') no-repeat 90% 90%;
}
.service-item.aerial-video {
	background-color: #bfb8a6;
}
.service-name {
	margin-bottom: 35px;
	font-size: 36px;
	font-weight: bold;
	line-height: 1.1;
}
.service-desc {
	margin-bottom: 40px;
	line-height: 1.3;
}
.read-more {
	color: #fff;
	border-bottom: 1px solid #fff;
}
.read-more:hover {
	padding-left: 5px;
	color: #fff;
	border-bottom-color: transparent;
}
/*** Features ***/
#features .container-fluid {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: center;
	padding-bottom: 105px;
}
.feature-item {
  flex: 0 0 30%;
  max-width: 400px;
	height: 250px;
	margin-right: 30px;
	padding-left: 100px;
	padding-top: 50px;
	background: url('../images/icons/icon-feature-budget.png') no-repeat left center;
  @media screen and (max-width: 560px) {
    padding-left: 0px;
    margin-right: 0px;
    flex: 0 0 100%;
    padding: 10%;
  }
}
.feature-item:last-child {
	margin-right: 0;
}
.feature-item.feature-budget {
	background: url('../images/icons/icon-feature-budget.png') no-repeat left center;
}
.feature-item.feature-control {
	background: url('../images/icons/icon-feature-control.png') no-repeat left center;
}
.feature-item.feature-video {
	background: url('../images/icons/icon-feature-video.png') no-repeat left center;
}
.feature-title {
	margin-bottom: 10px;
	font-size: 36px;
	font-weight: bold;
}
.feature-desc {
	line-height: 1.3;
}
/*** Our works ***/
#our-works {
	padding-top: 52px;
	padding-bottom: 70px;
	background-color: #141414;
}
#our-works > .container {
	//max-width: 1535px;
}
.our-works-nav {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: center;
	margin-bottom: 44px;
  flex-wrap: wrap;
}
.our-works-nav li {
	margin-right: 37px;
}
.our-works-nav li:last-child {
	margin-right: 0;
}
.our-works-nav li a {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: center;
	-ms-align-items: center;
	align-items: center;
	height: 49px;
	min-width: 151px;
	padding: 0 17px;
	font-size: 16px;
	font-weight: 500;
	color: #bfb8a6;
	border: 2px solid #bfb8a6;
}
.our-works-nav li.active a,
.our-works-nav li a:hover {
	color: #141414;
	background-color: #fff;
	border-color: #fff;
}
#our-works .item {
	outline: none;
}
.our-works-slider .slick-slide {
	margin: 0 22px;
}
.our-works-slider .slick-list {
	margin: 0 -22px;
}
.our-works-slider .slick-track {
	margin-bottom: 30px;
}
.our-works-slider .item img {
	margin-bottom: 35px;
}
.our-works-slider .item .work-title {
	font-size: 24px;
	font-weight: normal;
	text-align: center;
	color: #fff;
}
.our-works-slider .slick-dots li {
	margin: 0 10px;
}
.our-works-slider .slick-dots li.slick-active button {
	width: 16px;
	height: 16px;
	background-color: #bfb8a6;
}
.our-works-slider .slick-dots button {
	display: block;
	width: 12px;
	height: 12px;
	padding: 0;
	border: none;
	border-radius: 100%;
	background-color: #2e2e2e;
	text-indent: -9999px;
}
.our-works-slider .slick-prev,
.our-works-slider .slick-next {
	width: 34px;
	height: 34px;
	top: 170px;
	z-index: 2;
}
.our-works-slider .slick-prev {
	left: -45px;
}
.our-works-slider .slick-next {
	right: -45px;
}
.our-works-slider .slick-prev:before,
.our-works-slider .slick-next:before {
	font-size: 34px;
	transition: all .3s;
}
#our-works h2 {
	margin-bottom: 37px;
	font-size: 30px;
	color: #fff;
	text-align: center;
}

#contacts {
	margin-bottom: 100px;
	background: rgb(20,20,20);
	background: -webkit-linear-gradient(left, rgba(20,20,20,0) 50%, rgba(20,20,20,1) 50%, rgba(20,20,20,1) 100%);
	background: -o-linear-gradient(left, rgba(20,20,20,0) 50%, rgba(20,20,20,1) 50%, rgba(20,20,20,1) 100%);
	background: linear-gradient(to right, rgba(20,20,20,0) 50%, rgba(20,20,20,1) 50%, rgba(20,20,20,1) 100%);
}
.contacts-block {
	position: relative;
	//width: 1000px;
	//min-height: 764px;
	padding: 72px 185px;
	margin-left: auto;
	background-color: #141414;
}
.contacts-block h3 {
	margin-bottom: 40px;
	font-size: 36px;
	font-weight: bold;
	color: #fff;
}
.contacts-block form {
	width: 444px;
}
.contacts-block .btn-contacts {
	margin-left: auto;
	margin-right: auto;
	font-size: 18px;
	font-weight: bold;
	color: #141414;
	background-color: #bfb8a6;
}
.contacts-block .btn-contacts:hover {
	color: #bfb8a6;
	background-color: #141414;
}
.contacts-block .email,
.contacts-block .phone {
	position: absolute;
	font-size: 100px;
	line-height: 0.75;
	font-weight: 500;
}
.contacts-block .email {
	left: 0;
	bottom: 0;
	color: #fff;
}
.contacts-block .phone {
	bottom: 0;
	left: 1px;
	transform-origin: bottom left;
	transform: rotate(-90deg);
	font-weight: bold;
}

/*** Footer ***/
footer {
	padding: 20px 0;
	background-color: #141414;
}
footer .container {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-ms-align-items: center;
	align-items: center;
	justify-content: space-between;
}
.main-footer {
	margin-bottom: 50px;
}
.footer-menu {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
  flex-wrap: wrap;
}
.footer-menu li {
	margin-right: 39px;
}
.footer-menu li:last-child {
	margin-right: 0;
}
.footer-menu li a {
	font-size: 16px;
