}
footer .copyrights {
	justify-content: center;
	margin-bottom: 25px;
}
.copyrights a {
	font-size: 12px;
	font-weight: bold;
	color: #fff;
}
.copyrights a:hover {
	color: #bfb8a6;
}
.copyrights a.tos {
	margin-right: 60px;
	font-weight: 500;
	color: #bfb8a6;
	border-bottom: 1px solid #bfb8a6;
}
.copyrights a.tos:hover {
	border-bottom-color: transparent;
}

.our-works-slider {
  .slick-arrow {
    top: 35%;
    &:before {
      font-family: 'FontAwesome';
    }
    &.slick-next {
      right: -20px;
      &:before {
        content: '\f054';
      }
    }
    &.slick-prev {
      left: -20px;
      &:before {
        content: '\f053';
      }
    }
  }
}

.text-center {
  text-align: center;
}

